<template>
  <v-container fill-height class="py-5 grey lighten-3">
    <v-layout justify-center>
      <v-card raised>
        <v-toolbar flat>
          <v-toolbar-title>Input Data Aturan</v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <v-select v-model="item.suhu" :items="options.suhu" label="Suhu" />
          <v-select v-model="item.tekananUdara" :items="options.tekananUdara" label="Tekanan Udara" />
          <v-select v-model="item.kecepatanAngin" :items="options.kecepatanAngin" label="Kecepatan Angin" />
          <v-select v-model="item.kelembapan" :items="options.kelembapan" label="Kelembapan" />
          <v-select v-model="item.keterangan" :items="options.keterangan" label="Keterangan" />
        </v-card-text>
        <v-card-actions>
          <v-btn @click="create" flat dark color="green">Simpan</v-btn>
          <v-btn @click="$router.back()" flat dark color="pink">Batal</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-container>
</template>

<script>
import * as options from "@/models/options";
import { Rule__create } from "@/services/rules";
import evbus from "@/evbus";

export default {
  name: 'AddRule',
  data: () => ({
    item: {
      suhu: options.suhu[0],
      kecepatanAngin: options.kecepatanAngin[0],
      kelembapan: options.kelembapan[0],
      tekananUdara: options.tekananUdara[0],
      keterangan: options.keterangan[0]
    },
    options
  }),
  methods: {
    async create() {
      const payload = { ...this.item };
      try {
        await Rule__create(payload);
        evbus.$emit("error", "Sukses menambah data aturan");
        this.$router.back();
      } catch (err) {
        console.log(err);
        evbus.$emit("error", "Gagal menambah data aturan");
      }
    }
  }
}
</script>

